import {
    Button,
    Divider,
    Grid,
    IconButton,
    makeStyles,
    Paper,
    TextField,
    Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import { Form, Field } from "react-final-form";
import { FieldArray } from "react-final-form-arrays";
import arrayMutators from "final-form-arrays";
import { useDispatch, useSelector } from "react-redux";
import {
    playgroundUniqueUrl,
    sportsListUrl,
    generateSnackData,
    complexAddUrl,
    complexEditUrl,
    weekData,
} from "../../../assets/helper/constants";
import { commonGetRequest } from "../../../redux/features/Auth";
import SelectPricing from "../../common/SelectPricing";
import { setSnackBar, setIsLoading, setSteps } from "../../../redux/features/Auth";
import Server from "../../../Server";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import { useHistory } from "react-router";
import Submit from "../../common/Button/BlueContainedRounded";

import { Card } from "@material-ui/core";
import { CardContent } from "@material-ui/core";
import DatePicker from "../../common/DatePicker";
import Input from "../../common/Input/LoginOutlinedInput";

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
        backgroundColor: "#F5F5F5",
        borderColor: "#fff",
        display: "flex",
    },
    paper2: {
        padding: theme.spacing(2),
        marginLeft: theme.spacing(3),
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        backgroundColor: "#F3F3F3",
    },
    typo1: {
        color: "#484848",
        verticalAlign: "middle",
        fontWeight: "bold",
    },
    typo2: {
        paddingBottom: theme.spacing(1),
        color: "#484848",
        verticalAlign: "middle",
        fontWeight: "bold",
    },
    typo3: {
        paddingBottom: theme.spacing(1),
        color: "#808080",
        verticalAlign: "middle",
    },
    leftMargin: {
        marginLeft: theme.spacing(3),
        marginTop: theme.spacing(2),
    },
    divider: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    grid1: {
        display: "flex",
    },
    right: {
        marginLeft: "auto",
    },
    icon: {
        color: "#272727",
        marginLeft: "auto",
    },
    textfield: {
        backgroundColor: "#fff",
        width: "95%",
        marginBottom: theme.spacing(1),
    },
    paddingLeft: {
        paddingLeft: theme.spacing(3),
    },
    button: {
        borderRadius: 50,
        marginTop: theme.spacing(2),
        backgroundColor: theme.palette.base1.main,
        color: "#fff",
        "&:hover": {
            backgroundColor: theme.palette.base1.dark,
        },
    },
    paddingTop: {
        paddingTop: theme.spacing(1),
    },
    w100: {
        width: "100%"
    },
    mtop0: {
        marginTop: 0
    }
}));

const Forms = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const classes = useStyles();
    const required = (value) => (value ? undefined : "* Required");
    const { sports_list, complex_list, isLoading, playground_data } = useSelector(
        (state) => state.auth
    );
    let [globalForm, setGlobalForm] = React.useState({});

    let form = new FormData();

    const [fileData, setFileData] = useState(null);

    const [fromDate, setFromDate] = useState(new Date());
    const [toDate, setToDate] = useState(new Date());

    React.useEffect(() => {
        dispatch(setIsLoading());
        setGlobalForm({
            sports_id: "",
            complex_id: complex_list.complex_id,
            from_date: "",
            to_date: "",
            slots: [
                {
                    start_time: "",
                    end_time: "",
                    hourly_pricing: "",
                    total_pricing: "",
                    discount: "",
                    sports_id: "",
                },
            ],
        });
        dispatch(setIsLoading());
    }, []);

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: sportsListUrl,
                key: "sports_list",
            })
        );
    }, []);

    React.useEffect(() => {
        if (history.location.state && history.location.state.id) {
            dispatch(
                commonGetRequest({
                    url: playgroundUniqueUrl,
                    key: "playground_data",
                    query: "/" + history.location.state.id
                })
            );
        }
    }, [dispatch, history.location.state])

    React.useEffect(() => {
        dispatch(setIsLoading());
        let formatedData = {};
        if (playground_data) {
            formatedData.id = playground_data.id;
            formatedData.playground_name = playground_data.playground_name;
            formatedData.sports_id = playground_data.sport.id;
            formatedData.complex_id = playground_data.vendor_complex_id;
            formatedData.from_date = playground_data.playground_timeslots.length > 0 ? new Date(playground_data.playground_timeslots[0].date) : null;
            formatedData.to_date = playground_data.playground_timeslots.length > 0 ? new Date(playground_data.playground_timeslots[playground_data.playground_timeslots.length - 1].date) : null;
            formatedData.max_player_limit = playground_data.max_player_limit;
            formatedData.min_player_limit = playground_data.min_player_limit;
            formatedData.weekend_discount = playground_data.weekend_discount;
            formatedData.holiday_discount = playground_data.holiday_discount;

            setFromDate(playground_data.playground_timeslots.length > 0 ? new Date(playground_data.playground_timeslots[0].date) : new Date());
            setToDate(playground_data.playground_timeslots.length > 0 ? new Date(playground_data.playground_timeslots[playground_data.playground_timeslots.length - 1].date) : new Date());

            let slotsArray = [];
            playground_data.playground_timeslots.map(function (slotsData) {
                let isExists = slotsArray.find(dd => dd.start_time === slotsData.from_time && dd.end_time === slotsData.to_time && dd.sports_id === slotsData.sports_id && dd.total_pricing === slotsData.total_amount);
                if (!isExists) {
                    slotsArray.push(
                        {
                            start_time: slotsData.from_time,
                            end_time: slotsData.to_time,
                            total_pricing: slotsData.total_amount,
                            discount: slotsData.discount_amount,
                            sports_id: slotsData.sports_id,
                        }
                    )
                }
                return true;
            })

            formatedData.slots = slotsArray;
            setGlobalForm(formatedData);
            dispatch(setIsLoading());
        }
    }, [playground_data, dispatch])

    const onSubmit = async (values) => {
        values.from_date = fromDate;
        values.to_date = toDate;
        values.slots = JSON.stringify(values.slots);

        for (const key in values) {
            if (key !== "image") form.append(key, values[key]);
        }

        form.append("image", fileData);

        dispatch(setIsLoading());
        try {
            let res = {};
            res = await Server.post(globalForm && globalForm.id ? complexEditUrl : complexAddUrl, form);

            if (res.data && res.data.status) {
                dispatch(
                    setSnackBar(generateSnackData(true, res.data.message, "success"))
                );
                history.push("/playground");
            } else {
                dispatch(
                    setSnackBar(generateSnackData(true, res.data.message, "error"))
                );
            }
            dispatch(setIsLoading());
        } catch (error) {
            const resp =
                error.response && error.response.data.message
                    ? error.response.data.message
                    : "Couldn't connect to our servers.";

            dispatch(setSnackBar(generateSnackData(true, resp, "error")));
            dispatch(setIsLoading());
        }
    }

    const package_select_turf = ({ input, meta }) => {
        return (
            <TextField
                id="playground_name"
                label="Enter Playground Name"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
                className={classes.w100}
            />
        );
    };
    const min_player_limit = ({ input, meta }) => {
        return (
            <TextField
                id="min_player_limit"
                label="Enter Minimum Player Limit"
                type="number"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
                className={classes.w100}
            />
        );
    };
    const max_player_limit = ({ input, meta }) => {
        return (
            <TextField
                id="max_player_limit"
                label="Enter Maximum Player Limit"
                type="number"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
                className={classes.w100}
            />
        );
    };
    const weekend_discount = ({ input, meta }) => {
        return (
            <TextField
                id="weekend_discount"
                label="Enter Weekend Discount"
                type="number"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
                className={classes.w100}
            />
        );
    };
    const holiday_discount = ({ input, meta }) => {
        return (
            <TextField
                id="holiday_discount"
                label="Enter Holiday Discount"
                type="number"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
                className={classes.w100}
            />
        );
    };
    const package_select_sport = ({ input, meta }) => {
        return (
            <SelectPricing
                data={sports_list}
                funct="select"
                title="Select Sport"
                input={input}
                meta={meta}
                returnValue={(value) => console.log(value, input)}
                className={classes.w100}
            />
        );
    };
    const package_slot_start_time = ({ input, meta }) => {
        return (
            <TextField
                className={[classes.textfield, classes.w100]}
                id="from_time"
                label="6:00:00"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
            />
        );
    };
    const package_slot_end_time = ({ input, meta }) => {
        return (
            <TextField
                className={[classes.textfield, classes.w100]}
                id="to_time"
                label="18:00:00"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
            />
        );
    };
    const package_slot_hourly_price = ({ input, meta }) => {
        return (
            <TextField
                className={[classes.textfield, classes.w100]}
                id="hourly_price"
                label="Hourly Price"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
            />
        );
    };
    const package_slot_extension_pricing = ({ input, meta }) => {
        return (
            <TextField
                className={[classes.textfield, classes.w100]}
                id="total_amount"
                label="Total Pricing"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
            />
        );
    };
    const package_slot_volume_discount = ({ input, meta }) => {
        return (
            <TextField
                className={[classes.textfield, classes.w100]}
                id="discount_amount"
                label="Discount Price"
                type="input"
                variant="outlined"
                onChange={(event, value) => {
                    input.onChange(event.target.value);
                }}
                error={meta && meta.touched && meta.error ? true : false}
                helperText={meta && meta.touched && meta.error ? meta.error : ""}
                value={input?.value || input?.value === 0 ? input?.value : ""}
            />
        );
    };
    const package_slot_discount = ({ input, meta }) => {
        return (
            <SelectPricing
                data={sports_list}
                funct="select"
                input={input}
                meta={meta}
                returnValue={(value) => console.log(value, input)}
                className={classes.w100}
            />
        );
        // return (
        //   <TextField
        //     className={classes.textfield}
        //     id="sports_id"
        //     label="Sport"
        //     type="input"
        //     variant="outlined"
        //     onChange={(event, value) => {
        //       input.onChange(event.target.value);
        //     }}
        //     error={meta && meta.touched && meta.error ? true : false}
        //     helperText={meta && meta.touched && meta.error ? meta.error : ""}
        //     value={input?.value || input?.value === 0 ? input?.value : ""}
        //   />
        // );
    };


    const image = ({ input, meta }) => {
        return (
            <Input
                id="image"
                type="file"
                file={true}
                input={input}
                meta={meta}
                setFile={(file) => {
                    setFileData(file);
                }}
                className={[classes.w100, classes.mtop0]}
            />
        );
    };

    return (
        <Paper className={classes.paper}>
            {
                !isLoading ? (
                    <Form
                        initialValues={globalForm}
                        onSubmit={onSubmit}
                        mutators={{
                            ...arrayMutators,
                        }}
                        render={({
                            handleSubmit,
                            submitting,
                            values,
                            form: {
                                mutators: { push, pop },
                            },
                        }) => (
                            <form onSubmit={handleSubmit}>
                                {globalForm ? (
                                    <input type="hidden" name="id" value={globalForm.id} />
                                ) : null}
                                <Grid
                                    container
                                    md={12}
                                    className={(classes.grid1, classes.paddingLeft)}
                                >

                                    <Grid md={3} className={[classes.paddingLeft, classes.w100]}>
                                        <Field
                                            className={classes.textField}
                                            name="sports_id"
                                            component={package_select_sport}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid md={3} className={[classes.paddingTop, classes.w100]}>
                                        <Field
                                            className={classes.textField}
                                            name="playground_name"
                                            component={package_select_turf}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid md={3} className={[classes.paddingTop, classes.w100]}>
                                        <Field
                                            className={classes.textField}
                                            name="min_player_limit"
                                            component={min_player_limit}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid md={3} className={[classes.paddingTop, classes.w100]}>
                                        <Field
                                            className={classes.textField}
                                            name="max_player_limit"
                                            component={max_player_limit}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid md={3} className={[classes.paddingTop, classes.w100]}>
                                        <Field
                                            className={classes.textField}
                                            name="weekend_discount"
                                            component={weekend_discount}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid md={3} className={classes.paddingTop}>
                                        <Field
                                            className={classes.textField}
                                            name="holiday_discount"
                                            component={holiday_discount}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid
                                    container
                                    md={12}
                                    className={(classes.grid1, classes.paddingLeft)}
                                >
                                    <Grid md={3} className={classes.paddingTop}>
                                        <Typography className={classes.typo3}>Playground Image</Typography>
                                        <Field
                                            name="image"
                                            component={image}
                                            placeholder="Playgroudn image"
                                        />
                                    </Grid>
                                    <Grid className={classes.paddingTop}>
                                        <Typography className={classes.typo3} >
                                            From Date
                                        </Typography>
                                        <DatePicker
                                            fullWidth={1}
                                            value={globalForm ? globalForm.from_date : new Date()}
                                            changeFormData={(newData) => setFromDate(newData.date)}
                                        />
                                    </Grid>
                                    <Grid className={classes.paddingTop}>
                                        <Typography className={classes.typo3}>
                                            To Date
                                        </Typography>
                                        <DatePicker
                                            fullWidth={1}
                                            value={globalForm ? globalForm.to_date : new Date()}
                                            changeFormData={(newData) => setToDate(newData.date)}
                                        />

                                    </Grid>
                                </Grid>

                                <Grid
                                    container
                                    md={12}
                                    className={(classes.grid1, classes.paddingLeft)}
                                >
                                    <Grid className={classes.paddingTop}>
                                        <IconButton
                                            onClick={() => push("slots", undefined)}
                                            className={classes.icon}
                                            aria-label="create pricing"
                                            component="span"
                                        >
                                            <ControlPointIcon />
                                            <Typography className={classes.typo2}>
                                                ADD SLOTS
                                            </Typography>
                                        </IconButton>
                                    </Grid>
                                </Grid>
                                {/* <Grid lg={6} md={12}>
                                <Paper className={classes.paper2} elevation={0}> */}
                                <FieldArray name="slots">
                                    {({ fields }) =>
                                        fields.map((name, index) => (
                                            <Grid lg={12} md={12}>
                                                <Paper className={classes.paper2} elevation={0}>
                                                    <Grid lg={12} className={classes.grid1}>
                                                        <Grid lg={3}>
                                                            <Typography className={classes.typo3}>
                                                                Start Time (24 Hour)
                                                            </Typography>

                                                            <Field
                                                                name={`${name}.start_time`}
                                                                component={package_slot_start_time}
                                                            />
                                                        </Grid>
                                                        <Grid lg={3}>
                                                            <Typography className={classes.typo3}>
                                                                End Time (24 Hour)
                                                            </Typography>
                                                            <Field
                                                                name={`${name}.end_time`}
                                                                component={package_slot_end_time}
                                                            />
                                                        </Grid>
                                                        {/* <Grid lg={4}>
                                                            <Typography className={classes.typo3}>
                                                                Hourly Pricing
                                                            </Typography>
                                                            <Field
                                                                name={`${name}.hourly_pricing`}
                                                                component={package_slot_hourly_price}
                                                            />
                                                        </Grid> */}
                                                        <Grid lg={3}>
                                                            <Typography className={classes.typo3}>
                                                                Total Pricing
                                                            </Typography>

                                                            <Field
                                                                name={`${name}.total_pricing`}
                                                                component={package_slot_extension_pricing}
                                                            />
                                                        </Grid>
                                                        <Grid lg={3}>
                                                            <Typography className={classes.typo3}>
                                                                Discount
                                                            </Typography>
                                                            <Field
                                                                name={`${name}.discount`}
                                                                component={package_slot_volume_discount}
                                                            />
                                                        </Grid>
                                                        <Grid lg={4}>
                                                            <Typography className={classes.typo3}>
                                                                Select Sport
                                                            </Typography>
                                                            <Field
                                                                name={`${name}.sports_id`}
                                                                component={package_slot_discount}
                                                            />
                                                        </Grid>
                                                        {index !== 0 ? (
                                                            <span
                                                                onClick={() => fields.remove(index)}
                                                                style={{ cursor: "pointer" }}
                                                            >
                                                                
                                                            </span>
                                                        ) : null}
                                                    </Grid>
                                                </Paper>
                                                <Divider />
                                            </Grid>
                                        ))
                                    }
                                </FieldArray>
                                <Grid
                                    lg={6}
                                    md={12}
                                    className={(classes.grid1, classes.paddingLeft)}
                                >
                                    <Button
                                        type="submit"
                                        className={classes.button}
                                        disabled={submitting ? submitting : false}
                                    >
                                        Save
                                    </Button>
                                </Grid>
                            </form>
                        )
                        }
                    />
                )
                    : null
            }
        </Paper >
    );
};

export default Forms;
