import React from "react";
import Table from "../../common/DynamicTable";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";

import { useHistory } from "react-router";

import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, setSnackBar, setIsLoading } from "../../../redux/features/Auth";

import Server from "../../../Server";

import {
    playgroundListUrl,
    generateSnackData,
    playgroundDeleteUrl,
    serverUrl,
} from "../../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
    typo1: {
        color: "#F3555E",
        fontWeight: "600",
    },
    typo2: {
        color: "#808080",
        fontWeight: "500",
        fontSize: "18px",
    },
    typo3: {
        color: "#808080",
        fontWeight: "700",
        fontSize: "18px",
    },
}));

const columns = [
    { id: "id", label: "Playground Id", minWidth: 100 },
    { id: "name", label: "Playground Name", minWidth: 120 },
    { id: "image", label: "Image", minWidth: 100 },
    { id: "sports", label: "Sports", minWidth: 120, align: "center", },
    { id: "complex_name", label: "Vendor Complex Name", minWidth: 120, },
    { id: "created_on", label: "Created At", minWidth: 120, },
    { id: "action", label: "Action", minWidth: 120, },
];

const List = (props) => {
    const classes = useStyles();

    const { playgroundList, isLoading } = useSelector(
        (state) => state.auth
    );
    const dispatch = useDispatch();

    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: playgroundListUrl,
                key: "playgroundList",
                query: `?pageNo=${page}&perPageRecords=${rowsPerPage}&search=${props.searchVal ? props.searchVal : ''}`
            })
        );
    }, [dispatch, props.searchVal, page, rowsPerPage]);

    const styleId = (id) => {
        return (
            <Typography variant="h5" className={classes.typo1}>
                {id}
            </Typography>
        );
    };

    const styleOthers = (value, bold) => {
        return <Typography className={bold ? classes.typo3 : classes.typo2}>{value}</Typography>;
    };

    const styleImage = (value, bold) => {
        return <img src={serverUrl + value} alt="Playground Icon" width={100} height={100} />;
    };

    const deleteRow = async (id) => {
        if (window.confirm("Are you sure you want to delete this?")) {
            dispatch(setIsLoading());
            try {
                let res = await Server.delete(playgroundDeleteUrl + "/" + id);

                if (res.data && res.data.status) {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                } else {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
                }
                dispatch(setIsLoading());
                dispatch(
                    commonGetRequest({
                        url: playgroundListUrl,
                        key: "playgroundList",
                        query: `?pageNo=${page}&perPageRecords=${rowsPerPage}&search=${props.searchVal}`
                    })
                );
            } catch (error) {
                const resp =
                    error.response && error.response.data.message
                        ? error.response.data.message
                        : "Couldn't connect to our servers.";

                dispatch(setSnackBar(generateSnackData(true, resp, "error")));
                dispatch(setIsLoading());
            }
        }
    }

    const history = useHistory();
    const editRow = (id) => {
        history.push("/playground-add", { id: id })
    }

    const styleAction = (id) => {
        return (
            <div>
                <IconButton onClick={() => deleteRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <DeleteOutlineIcon fontSize="medium" />
                </IconButton>
                <IconButton onClick={() => editRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <EditOutlinedIcon fontSize="medium" />
                </IconButton>
            </div>
        );
    };

    function createData(oldid, oldname, oldimage, oldsports, oldcomplex, oldcreated, oldaction) {
        const id = styleId(oldid);
        const name = styleOthers(oldname);
        const image = styleImage(oldimage);
        const sports = styleOthers(oldsports);
        const complex_name = styleOthers(oldcomplex, 1);
        const created_on = styleOthers(oldcreated);
        const action = styleAction(oldaction);
        return { id, name, image, sports, complex_name, created_on, action };
    }
    let rows = [];

    if (!isLoading && playgroundList) {
        playgroundList.map(function (playData) {
            rows.push(
                createData(
                    playData.id,
                    playData.playground_name,
                    playData.image,
                    playData.sport.name,
                    playData.vendor_complex.complex_name,
                    playData.created_on,
                    playData.id
                )
            )
            return true;
        })
    }

    return <Table
        columns={columns}
        rows={rows}
        page={page}
        rowsPerPage={rowsPerPage}
        setPage={(pp) => setPage(pp)}
        setRowsPerPage={(rp) => setRowsPerPage(rp)}
    />;
};

export default List;
